<template>
  <div>
    <div @click="goToPost" class="postBlock">
      <div class="postContent">
        <h3>{{ title }} -- {{ user }} ({{ getDate(timeCreated) }})</h3>
        <p>{{ content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "postComponent",
  props: {
    title: String,
    content: String,
    user: String,
    timeCreated: String,
    postID: Number,
  },
  methods: {
    getDate(timestamp) {
      return new Date(Number(timestamp)).toLocaleDateString();
    },
    goToPost() {
      fetch(`/api/post/${this.postID}`, { method: "POST" })
        .then((res) => res.json())
        .then((data) => {
          this.$store.commit("setAvailableReplies", data);
        });
      // this.$store.commit("setCurrentPostID", this.postID);
      this.$store.commit("setCurrentPost", {
        title: this.title,
        content: this.content,
        id: this.postID,
      });
    },
  },
};
</script>

<style scoped>
.postBlock {
  background: var(--light-second-color);
  border-radius: 10px;
}

.postContent {
  margin: 10px;
  width: max-content;
}
</style>