<template>
  <div>
    <div class="nav">
      <div class="nav-item homeButton">
        <button @click="setPage('Home')">Home</button>
      </div>
      <div v-if="!this.$store.state.isLoggedIn" class="nav-item loginButton">
        <button @click="setPage('Login')">Login</button>
      </div>
      <div v-else class="nav-item userDetails">
        <button @click="logout">Log Out</button>
        <p>{{ this.$store.state.currentUserName }}</p>
        <img :src="this.$store.state.currentUserPFP" alt="" />
      </div>
    </div>
    <div v-if="this.$store.getters.pageState == 'Home'">
      <Home />
    </div>
    <div v-else-if="this.$store.getters.pageState == 'Login'">
      <Login />
    </div>
    <div v-else-if="this.$store.getters.pageState == 'forum'">
      <Forum />
    </div>
    <div v-else-if="this.$store.getters.pageState == 'Post'">
      <postPage />
    </div>
  </div>
</template>

<script>
import Home from "./components/Home.vue";
import Login from "./components/Login.vue";
import Forum from "./components/ForumPage.vue";
import postPage from "./components/post-page-component.vue";

export default {
  name: "App",
  components: { Home, Login, Forum, postPage },
  mounted() {
    fetch("/api/forums", { method: "POST" })
      .then((res) => res.json())
      .then((data) => {
        this.$store.commit("setForums", data);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    setPage(page) {
      this.$store.commit("switchPage", page);
    },
    logout() {
      this.$store.commit("logout");
    },
  },
};
</script>

