<template>
  <div>
    <button @click="findForum">{{ forum.token }} : {{ forum.title }}</button>
  </div>
</template>

<script>
export default {
  name: "forumLink",
  props: { forum: Object },
  methods: {
    findForum() {
      fetch(`/api/forum/${this.forum.token}`, {
        method: "POST",
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(typeof data);
          this.$store.dispatch("setCurrentForumData", [this.forum.token, data]);
        });
    },
  },
};
</script>

<style scoped>
button {
  background-color: var(--pale-first-color);
  border: solid var(--dark-color);
  border-radius: 15px;
  color: var(--dark-color);
  padding: 15px 32px;
  margin: 5px 0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
</style>