<template>
  <div>
    <div v-if="isPosting">
      <replypostcover />
    </div>
    <h1>{{ currentPostContent.title }}</h1>
    <p>{{ currentPostContent.content }}</p>
    <div v-for="reply in postReplies" :key="reply.id">
      <replyComponent :content="reply.content" :user="reply.user" />
    </div>
    <div class="addReply" v-if="this.$store.state.isLoggedIn">
      <input type="text" v-model="replyContent" />
      <button @click="sendReply">Add Reply</button>
    </div>
    <div v-else>
      <h2>Please Log In To Reply!</h2>
    </div>
  </div>
</template>

<script>
import replyComponent from "./reply-component.vue";
import replypostcover from "./reply-post-cover.vue";
export default {
  name: "postPage",
  data() {
    return {
      replyContent: "",
    };
  },
  components: { replyComponent, replypostcover },
  computed: {
    postReplies() {
      return this.$store.state.postReplies;
    },
    isPosting() {
      return this.$store.state.isAddingPost;
    },
    currentPostContent() {
      return this.$store.state.currentPost;
    },
    // currentPostID() {
    //   return this.$store.state.currentPostID;
    // },
  },
  methods: {
    sendReply() {
      console.log(`Reply ${this.replyContent}`);
      const data = {
        content: this.replyContent,
        post: this.currentPostContent.id,
      };
      fetch(`/api/addReply`, {
        method: "POST",
        body: JSON.stringify(data),
        headers: { "content-type": "application/json" },
      }).then((res) => {
        console.log(res);
        this.$store.dispatch("updateCurrentPost");
        this.replyContent = "";
      });
    },
  },
};
</script>

