<template>
  <div class="login">
    <GoogleLogin :params="params" :onSuccess="onSuccess" :onFailure="onFailure"
      >Login</GoogleLogin
    >
  </div>
</template>

<script>
import { GoogleLogin } from "vue-google-login";
export default {
  name: "Login",
  components: {
    GoogleLogin,
  },
  data() {
    return {
      params: {
        client_id:
          "897864354621-el118c9a3qd729f7joaad2fitj5f4t8e.apps.googleusercontent.com",
      },
    };
  },
  methods: {
    onSuccess(googleUser) {
      const auth_token = googleUser.getAuthResponse().id_token;
      const data = { token: auth_token };
      fetch("/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      })
        .then((res) => res.json())
        .then((userObject) => {
          this.$store.commit("userLoggedIn", userObject);
          this.$store.commit("switchPage", "Home");
        });
    },
    onFailure() {
      console.log("fail");
    },
  },
};
</script>

