<template>
  <div>
    <div class="darkened">
      <div class="centered">
        <div class="inputs">
          <label for="postTitle">
            Post Title <br />
            <input
              type="text"
              name="postTitle"
              id="postTitle"
              v-model="newPostTitle"
            /> </label
          ><br />
          <label for="postContent">
            Post content<br />
            <textarea
              name="postContent"
              id="postContent"
              cols="24"
              rows="8"
              style="resize: none"
              v-model="newPostContent"
            ></textarea>
          </label>
          <br />
        </div>
        <div class="buttons">
          <button @click="submit" class="submit">Submit</button
          ><button @click="cancel" class="cancel">Cancel</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "postcover",
  data() {
    return {
      currentForum: this.$store.state.currentForum,
      newPostTitle: "",
      newPostContent: "",
    };
  },
  methods: {
    cancel() {
      this.$store.commit("setPostStatus", false);
    },
    async submit() {
      let data = {
        title: this.newPostTitle,
        content: this.newPostContent,
        forum: this.currentForum,
      };
      fetch("/api/addPost", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify(data),
      })
        .then((res) => res.json)
        .then((data) => {
          console.log(data);
          this.$store.dispatch("updateCurrentForum");
          this.$store.commit("setPostStatus", false);
        });
    },
  },
};
</script>

<style scoped>
.darkened {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(50, 50, 50, 50%);
}

.buttons {
  position: relative;
  width: max-content;
  left: 50%;
  transform: translateX(-50%);
}

.inputs {
  position: relative;
  width: min-content;
  left: 50%;
  transform: translateX(-50%);
}

.centered {
  display: inline-block;
  background-color: cornsilk;
  width: 50vw;
  height: 40vh;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: var(--dark-color);
  border-radius: 15px;
}

.submit {
  background-color: green;
  border: solid;
  border-radius: 15px;
  color: var(--light-first-color);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
}
.cancel {
  background-color: red;
  border: solid;
  border-radius: 15px;
  border-color: var(--dark-color);
  color: var(--light-first-color);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
}
</style>