<template>
  <div>
    <div v-if="isPosting == true">
      <postcover />
    </div>
    <h1>Forums</h1>
    <h4>{{ name }}</h4>
    <div v-for="post in posts" :key="post.id">
      <postComponent
        :title="post.title"
        :content="post.content"
        :user="post.name"
        :timeCreated="post.timeCreated"
        :postID="post.id"
      />
    </div>
    <div class="addPost">
      <button v-if="this.$store.state.isLoggedIn" @click="addPost">
        Ask Something!
      </button>
      <div v-else><p>Please Log In To Add A Post!</p></div>
    </div>
  </div>
</template>

<script>
import postcover from "./post-forum-cover.vue";
import postComponent from "./postComponent.vue";
export default {
  name: "Forum",
  data() {
    return {
      name: this.$store.state.currentForum,
    };
  },
  methods: {
    addPost() {
      this.$store.commit("setPostStatus", true);
    },
  },
  computed: {
    isPosting() {
      return this.$store.state.isAddingPost;
    },
    posts() {
      return this.$store.state.forumPosts;
    },
  },
  components: { postcover, postComponent },
};
</script>

<style scoped>
button {
  background-color: var(--light-second-color);
  border: solid;
  border-radius: 15px;
  color: var(--dark-color);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
</style>