<template>
  <div>
    <div class="titles">
      <h1>Home</h1>
      <h2>Available Forums</h2>
    </div>
    <div class="forums-wrapper">
      <div class="forumLink" v-for="forum in forums" :key="forum.id">
        <forumLink :forum="forum" />
      </div>
    </div>
  </div>
</template>

<script>
import forumLink from "./forum-link.vue";
export default {
  name: "Home",
  methods: {
    switchToLogin() {
      this.$store.commit("switchPage", "login");
    },
  },
  data() {
    return {
      forums: [],
    };
  },
  mounted() {
    setInterval(() => {
      this.forums = this.$store.state.availableForums;
    }, 0);
  },
  components: { forumLink },
};
</script>

<style scoped>
.forums-wrapper {
  display: grid;
  grid-template-columns: auto auto auto;
}

.forumLink {
  display: flex;
  justify-content: center;
  margin: 0;
  font-size: 2vh;
}

.titles {
  display: block;
  text-align: center;
}
</style>